.Card {
    border: 3px solid; 
    border-radius: 10px;
    overflow: hidden;
    margin: 10px;
    display: flex;
    flex-direction: column;
    min-width: 500px;
    max-width: 500px;
    
    
}

.Card .Title {
    padding: 05px 0;
    display: flex;
    justify-content: center;

}

.Card .Content {
    background-color: #FFF;
    color: #000;
    padding: 10px;
    border-radius: 10px;
    flex-grow: 1;
    margin: 5px;
    
        
    
}