body {
    font-family: Oswald;
    background-color: #222;
    color: #fff;
    text-align: center;
    font-size: 1.4rem;
    
}

h1, h2, h3 {
    font-weight: 200;
}

h2, h3 {
    margin: 0;
}
.App .Cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
}


.div-botao button {
    background-color: #424242;
    color: white;
    border-bottom: #424242;
    border-radius: 50px;
    width: 50px;
    height: 50px;
    font-size: 1.4rem;
    margin: 15px;
    
}
.Input input{
    font-size: 1.4rem;
    border-radius: 05px;
    width: 80%;
    
}
.b-filho {
    width: 250px;
    height: 30px;
    background-color:darkcyan;
    border-radius: 15px;
    color: white;
    margin: 2px;
}

.b-filho:hover {
    background-color: chartreuse;
    color: black;
    transition: 1s;
}
.desafioProdutos2 th {
    
    background-color: #008b8b;
}

.desafioProdutos2 table {
    width: 100%;
}

.desafioProdutos2 .Par {
    background-color: #ddd;
}

.desafioProdutos2 .Impar {
    background-color: #ccc;
}


    
.Card {
    border: 3px solid; 
    border-radius: 10px;
    overflow: hidden;
    margin: 10px;
    display: flex;
    flex-direction: column;
    min-width: 500px;
    max-width: 500px;
    
    
}

.Card .Title {
    padding: 05px 0;
    display: flex;
    justify-content: center;

}

.Card .Content {
    background-color: #FFF;
    color: #000;
    padding: 10px;
    border-radius: 10px;
    flex-grow: 1;
    margin: 5px;
    
        
    
}
